import artifactDebuffs from "../data/artifacts/artifactDebuffs";
import characters from "../data/characters";
import { ampRxnTrigElmts, baseRxnDmg, tfmRxns } from "../data/elements";
import { getElmtByIndex, getElmtByName } from "../helpers/misc";
import {
  calcBase,
  calcDmg,
  dmgFormula,
  getDefMult,
  getResMult,
  getXtraTalentLv,
  isInfused
} from "./helpers";

function getRdMult(char, charDCs, party, artDCs, elmtMCs, customDCs, target) {
  const { Level, ...RES } = target;
  const rdMult = { DEF: 0, DEFtoES: 0, ...RES };
  for (const { type, val } of customDCs) {
    if (type === "DEF reduction") {
      rdMult.DEF += val;
    } else {
      rdMult[type.slice(0, -10)] -= val;
    }
  }
  for (const { activated, inputs, index } of charDCs) {
    const debuff = getElmtByIndex(characters[char.name].debuffs, index);
    if (debuff && debuff.isGranted(char) && activated) {
      debuff.addPntes({ rdMult, fromSelf: true, char, inputs, party });
    }
  }
  for (const tm of party) {
    if (!tm) continue;
    for (const { activated, inputs, index } of tm.DCs) {
      if (activated) {
        const debuff = getElmtByIndex(characters[tm.name].debuffs, index);
        if (debuff) debuff.addPntes({ rdMult, fromSelf: false, inputs });
      }
    }
  }
  for (const ctrl of artDCs) {
    if (ctrl.activated) {
      const debuff = artifactDebuffs[ctrl.code][ctrl.index];
      debuff.addPntes({ rdMult, inputs: ctrl.inputs });
    }
  }
  const geoRsn = getElmtByName(elmtMCs.resonance, "Enduring Rock");
  if (geoRsn && geoRsn.activated) rdMult["Geo RES"] -= 20;
  if (elmtMCs.superconduct) rdMult["Physical RES"] -= 40;

  const { DEF, DEFtoES, ...others } = rdMult;
  for (let RES in others) {
    rdMult[RES] = getResMult(rdMult[RES] / 100);
  }
  return rdMult;
}

function getBaseDmg(ATTRs, dmgPiece, level, tlBnes) {
  const { xtraMult = 0 } = tlBnes;
  if (Array.isArray(dmgPiece.baseMult)) {
    return dmgPiece.baseMult.map((mult, i) =>
      calcBase(
        ATTRs,
        { ...dmgPiece, baseMult: mult },
        level,
        Array.isArray(xtraMult) ? xtraMult[i] : xtraMult
      )
    );
  }
  return calcBase(ATTRs, dmgPiece, level, xtraMult);
}

function getTotalFlat(tlBnes, [atkType, elmtType], bonuses) {
  return (
    (tlBnes.xtraFlat || 0) +
    (bonuses[atkType]?.Flat || 0) +
    (bonuses[elmtType]?.Flat || 0)
  );
}

function getDmgBnMult(tlBnes, [atkType, elmtType], bonuses, inf, ATTRs) {
  let normal = (tlBnes.xtraDmgBn || 0) + (bonuses[atkType]?.DMG || 0);
  if (isInfused(atkType, elmtType, inf)) {
    normal += ATTRs[inf + " DMG Bonus"];
  } else {
    normal += bonuses[elmtType]?.DMG || 0;
  }
  normal = 1 + (bonuses.AllDmg + normal) / 100;
  let special = 1;
  if (atkType && bonuses[atkType]?.mult) {
    // atkType can be null
    special = 1 + bonuses[atkType].mult / 100;
  }
  return [normal, special];
}

function getRxnMult(elmtMCs, elmtType, inf, rxnBnes, vision) {
  const { ampRxn, naAmpRxn } = elmtMCs;
  if (
    ampRxn &&
    (elmtType === "Elemental" ||
      (inf === vision && ampRxnTrigElmts[ampRxn].includes(inf)))
  ) {
    return rxnBnes[ampRxn];
  } else if (
    naAmpRxn &&
    inf !== vision &&
    ampRxnTrigElmts[naAmpRxn].includes(inf)
  ) {
    return rxnBnes["na" + naAmpRxn];
  }
  return 1;
}

function getReduction(char, target, rdMult, [atkType, elmtType], vision, inf) {
  let defMult = rdMult.DEF;
  if (atkType === "ES") {
    defMult += rdMult.DEFtoES;
  }
  defMult = getDefMult(+char.level.split("/")[0], target.Level, defMult);
  if (elmtType === "Elemental") {
    return [defMult, rdMult[vision + " RES"]];
  } else if (elmtType === "Physical" && !["NA", "CA", "PA"].includes(atkType)) {
    return [defMult, rdMult["Physical RES"]];
  } else if (elmtType === "Various") {
    return [defMult, 1];
  }
  return [defMult, rdMult[inf + " RES"]];
}

function getCrit(CR, CD, tlBnes, dmgTypes, bonuses) {
  const { xtraCR = 0, xtraCD = 0 } = tlBnes;
  const result = { CR: CR + xtraCR, CD: CD + xtraCD };
  const [atkType, elmtType] = dmgTypes;
  result.CR += (bonuses[atkType]?.CR || 0) + (bonuses[elmtType]?.CR || 0);
  result.CD += (bonuses[atkType]?.CD || 0) + (bonuses[elmtType]?.CD || 0);
  result.CR = Math.min(Math.max(result.CR, 0), 1);
  return result;
}

export default function getDmg(
  char,
  selfMCs,
  party,
  artDCs,
  ATTRs,
  bonuses,
  rxnBnes,
  customDCs,
  infusion,
  elmtMCs,
  target
) {
  const CR = ATTRs["CRIT Rate"] / 100;
  const CD = ATTRs["CRIT DMG"] / 100;
  const rdMult = getRdMult(
    char,
    selfMCs.DCs,
    party,
    artDCs,
    elmtMCs,
    customDCs,
    target
  );
  const { actvTalents, vision } = characters[char.name];

  const finalResult = {};
  actvTalents.forEach((talent) => {
    const { type } = talent;
    const level = char[type] + getXtraTalentLv(talent, char, party);
    finalResult[type] = {};

    for (const piece of talent.stats) {
      if (piece.noCalc) continue;
      const { dmgTypes, getTlBnes } = piece;
      const tlBnes = getTlBnes
        ? getTlBnes({ char, selfMCs, ATTRs, party })
        : {};
      let base = getBaseDmg(ATTRs, piece, level, tlBnes);

      if (base && dmgTypes) {
        const inf = infusion[dmgTypes[0]];
        const flat = getTotalFlat(tlBnes, dmgTypes, bonuses);
        const [normal, special] = getDmgBnMult(
          tlBnes,
          dmgTypes,
          bonuses,
          inf,
          ATTRs
        );
        const rxn = getRxnMult(elmtMCs, dmgTypes[1], inf, rxnBnes, vision);
        const [def, res] = getReduction(
          char,
          target,
          rdMult,
          dmgTypes,
          vision,
          inf
        );
        const crit = getCrit(CR, CD, tlBnes, dmgTypes, bonuses);
        finalResult[type][piece.name] = {
          "Non-crit": calcDmg(
            base,
            dmgFormula(flat, normal, special, rxn, def, res)
          ),
          get Crit() {
            return calcDmg(this["Non-crit"], (n) => n * (1 + crit.CD));
          },
          get Average() {
            return calcDmg(
              this["Non-crit"],
              (n) => n * (1 + crit.CR * crit.CD)
            );
          }
        };
      } else {
        let flat = 0;
        let normal = 1;
        if (piece.isHealing) {
          if (tlBnes?.xtraFlat) {
            flat = tlBnes.xtraFlat;
          }
          normal += ATTRs["Healing Bonus"] / 100;
        }
        base = (base + flat) * normal;
        if (piece.getLimit) {
          base = Math.min(base, piece.getLimit({ ATTRs }));
        }
        finalResult[type][piece.name] = {
          "Non-crit": base,
          Crit: 0,
          Average: base
        };
      }
    }
  });

  finalResult["Reactions DMG"] = {};
  for (let rxn in tfmRxns) {
    let base = baseRxnDmg[char.level.split("/")[0]];
    const normal = tfmRxns[rxn].mult;
    const special = 1 + rxnBnes[rxn] / 100;
    const { dmgType } = tfmRxns[rxn];
    const res = dmgType !== "Various" ? rdMult[dmgType + " RES"] : 1;
    base *= normal * special * res;
    finalResult["Reactions DMG"][rxn] = {
      "Non-crit": base,
      Crit: 0,
      Average: base
    };
  }
  return finalResult;
}
