import { useEffect, useState } from "react";
import styled from "styled-components";
import { colorCodes } from "../../../data/misc";
import { getNamesForTables } from "../../../helpers/misc";
import { Text } from "../../../styledCpns/DataDisplay";
import { Box } from "../../../styledCpns/Layout";
import { Collapse } from "../HiddenSpace";
import { MultiSetup, OneSetup } from "./Layouts";

const StyledDiv = styled.div`
  & > div:not(:last-child) {
    margin-bottom: 1rem;
  }
`;

export function DmgTables({ resetSignal, charName, dmg, focus }) {
  const [closed, setClosed] = useState([]);
  const namesForTables = getNamesForTables(charName);

  useEffect(() => {
    setClosed([]);
  }, [resetSignal]);

  return (
    <StyledDiv>
      {namesForTables.map((name, i) => {
        const stdValues = dmg[name.main];
        return (
          <DmgTable
            key={i}
            index={i}
            name={name}
            stdValues={stdValues}
            focus={focus}
            closed={closed}
            setClosed={setClosed}
          />
        );
      })}
    </StyledDiv>
  );
}

export function DmgTable({ index, name, stdValues, focus, closed, setClosed }) {
  return (
    <div className="dmg-table flex-col">
      <Box
        className="mx-auto px-3 mb-2 flex align-center cursor-default"
        borderRadius="1rem"
        bgColor="orange"
        onClick={() =>
          setClosed((prev) => {
            const newC = [...prev];
            newC[index] = !newC[index];
            return newC;
          })
        }
      >
        <Text variant="h5" fontWeight={700} color="black">
          {name.main}
        </Text>
        <Text className="ml-2" variant="subtitle1" color="black">
          <i className={"fas fa-chevron-" + (closed[index] ? "down" : "up")} />
        </Text>
      </Box>
      <Collapse in={!closed[index]}>
        <div className="custom-sb">
          <StyledTable>
            {focus ? (
              <MultiSetup name={name} stdValues={stdValues} focus={focus} />
            ) : (
              <OneSetup subNames={name.subs} stdValues={stdValues} />
            )}
          </StyledTable>
        </div>
      </Collapse>
    </div>
  );
}

// also [../../Canopy/ImportPending]
export const StyledTable = styled.table`
  margin-bottom: 0.5rem;
  min-width: 100%;
  border-collapse: collapse;
  font-size: 0.875rem;
  line-height: 1.43;
  th,
  td {
    padding: 0.25rem 0.5rem;
    border-left: 1px solid ${colorCodes.cardBg1};
    border-right: 1px solid ${colorCodes.cardBg1};
    cursor: default;
  }
  td:first-child {
    font-weight: 700;
  }
  td:not(:first-child) {
    text-align: right;
  }
  tr:nth-child(2n) {
    background-color: ${colorCodes.cardBg2};
  }
  tr:nth-child(2n + 1) {
    background-color: ${colorCodes.cardBg1};
  }
  tr:first-child {
    background-color: #01121b;
  }
  tr:not(:first-child):hover {
    background-color: #8a1200;
  }
  .lightGold-text {
    color: ${colorCodes.lightGold};
  }
  .sticky-col {
    position: sticky;
    z-index: 2;
    left: 0;
    min-width: 8.5rem;
    background-color: inherit;
  }
  .limited-width {
    width: 8.5rem;
  }
  .fas {
    position: absolute;
    top: 50%;
    right: 0.375rem;
    margin-top: -0.425rem;
    font-size: inherit;
  }
  .fa-long-arrow-alt-up,
  .higher {
    color: ${colorCodes.green};
  }
  .lower {
    color: #ff5d61;
  }
  .fa-long-arrow-alt-down {
    color: ${colorCodes.lightRed};
  }
  .tooltip_wrapper {
    padding-right: 1.125rem !important;
    min-width: 5rem;
  }
  .tooltip_wrapper:hover .tooltip {
    display: block;
  }
  .tooltip {
    position: absolute;
    bottom: calc(50% + 0.625rem);
    right: 1rem;
    z-index: 1;
    display: none;
    padding: 0.25rem 0.5rem 0.125rem;
    border-radius: 0.25rem;
    background: black;
  }
`;
